angular.module('jo-cobo', ['ui.router', 'firebase']).config(function($stateProvider, $urlRouterProvider){

  $stateProvider
    .state('home', {
      url: '/',
      templateUrl: '../views/homeTmp.html'
    })

    .state('fix', {
      url: '/fix',
      templateUrl: '../views/fixTmp.html'
    })

    .state('about', {
      url: '/about',
      templateUrl: '../views/aboutTmp.html'
    })

    $urlRouterProvider
      .otherwise('/')
})
